html {
	font-size: 16px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	overflow-x: hidden;
}

body {
	margin: 0 auto;
	width: 100%;
	font-family: "Nunito Sans", sans-serif;
}

a {
	color: rgb(0, 0, 0);
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}